* {
  box-sizing: border-box;
}

body {
  font-family: 'Proxima Nova';
  font-size: 20px;
  color: $textColor;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
b {
  margin: 0;
}

button {
  cursor: pointer;
}
